.notes__main-content{
    display: flex;
    flex-direction: column;
    height: 100%;
    /* width: 100%; */
    
    
}

.notes__appbar{
    align-items: center;
    background-color: $primary;
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px 10px 20px;
    


}

.notes__input-title, .notes__texArea {
    border: none;

    &:focus{
        outline: none;
    }
}



.notes__content{
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;
}

.notes__input-title{
    color: $dark-grey;
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 10px;
}

.notes__texArea{
    border: none;
    color: $dark-grey;
    font-size: 20px;
    flex: 1 1 auto;
    resize: none;

}

.notes__image img {
    box-shadow: 5px 5px $dark-grey;
    height: 150px;
}